import './vendor';

$(document).ready(function() {
	$('.popup-with-form').magnificPopup({
		type: 'inline',
		fixedContentPos: false,
		fixedBgPos: true,
		overflowY: 'auto',
		closeBtnInside: true,
		preloader: false,
		midClick: true,
		removalDelay: 300,
		mainClass: 'my-mfp-zoom-in'
	});

	$(".anchor").on("click", function (e) {
		e.preventDefault();
		var o = $(this).attr("href");
		if ($(o).length) {
			var s = $(o).offset().top;
			$("body,html").stop().animate({
				scrollTop: s
			}, 1500)
		} else console.warn("ID don't search!")
	});

	$('.portfolio-item').each(index => {
		$(`.portfolio-item_index_${ index } .portfolio-item__slider`).slick({
			infinite: true,
			adaptiveHeight: true,
			slidesToShow: 1,
			slidesToScroll: 1,
			prevArrow: `.portfolio-item_index_${ index } .arrow_prev`,
			nextArrow: `.portfolio-item_index_${ index } .arrow_next`,
		});
	});

	let inputs = document.querySelectorAll('.cost__form .input');
	let button = document.querySelector('.cost__form .button');

	function isValid() {
		let isValid = true;

		inputs.forEach(item => {
			if (item.validity.valueMissing) {
				isValid = false;

				item.classList.add('input_animated');
				setTimeout(() => {
					item.classList.remove('input_animated');
				}, 1000);
			}
		});

		return isValid
	}

	button.addEventListener('click', event => {
		event.preventDefault();
		if (isValid()) {
			$.magnificPopup.open({
				items: {
					src: '#cost-form',
				},
				type: 'inline'
			}, 0);
		}
	});

	var slickActive = false;

	function initMagnificPopup() {
		$('.certificates-item__link').magnificPopup({
			type: 'image',
			closeOnContentClick: true,
			closeBtnInside: false,
			fixedContentPos: true,
			mainClass: 'mfp-no-margins mfp-with-zoom', // class to remove default margin from left and right side
			image: {
				verticalFit: true
			},
			zoom: {
				enabled: true,
				duration: 300 // don't foget to change the duration also in CSS
			}
		});
	}

	function slickSize() {
		if ($(window).width() <= 975 && !slickActive) {
			initMagnificPopup();
			$('.certificates__list').slick({
				slidesToShow: 1,
				slidesToScroll: 1,
				arrows: false,
			});
			slickActive = true;
		} else if ($(window).width() > 975) {
			initMagnificPopup();

			if (slickActive) {
				$('.certificates__list').slick('unslick');
				slickActive = false;
			}
		}
	}
	$(window).on('load resize', slickSize);

	$('form').each(function () {
		$(this).submit(event => {
			event.preventDefault();
			send_form($(this));

			$.magnificPopup.open({
				items: {
					src: '#thanks'
				},
				type: 'inline'
			}, 0);
		
			setTimeout(() => {
				$.magnificPopup.close();
			}, 2000);
		});
	});
	
	function send_form(form) {
		var msg = form.serialize();
		$.ajax({
			type: 'POST',
			url: 'mail/send.php',
			data: msg,
			success: function (data) {
				if (data == "send_error") {
					alert('Возникла ошибка при отправке. Попробуйте пожалуйста еще раз.');
				}
			},
			error: function (xhr, str) {
				alert('Error: ' + xhr.responseCode + ' Please, try again later.');
			}
		});
	}
});

